@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap);
.fixed-overlay {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.fixed-overlay-modal {
  text-align: center;
  white-space: nowrap;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

.fixed-overlay-modal::after {
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
  content: "";
}

.modal {
  display: inline-block;
  vertical-align: middle;
}

.modal-container {
  padding: 50px;
  text-align: left;
  white-space: normal;
  background-color: #fff;
  border-radius: 12px;
  border: 1px solid #e9ecef;
  box-shadow: 0 30px 50px 0 rgba(0, 0, 0, 0.5);
}

.modal-button {
  display: block;
  letter-spacing: 1.5px;
}

.close {
  display: none;
}

.modal-form {
  display: flex;
  flex-direction: column;
  grid-gap: 24px;
  gap: 24px;
}

.modal-form-label {
  font-size: 12px;
}

.modal-form-input {
  margin-top: 6px;
  padding: 6px;
  border-radius: 6px;
  display: block;
}

.modal-form-button {
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #087f5b;
  background-color: #087f5b;
  padding: 8px;
  border-radius: 6px;
}

.modal-form-button:hover {
  cursor: pointer;
  background-color: #099268;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.4);
}

.modal-form-button:active {
  box-shadow: 0 1px 20px 0 rgba(0, 0, 0, 0.4);
}

.container-item {
  padding: 32px;
  border-radius: 12px;
  background-color: #fff;
  border: 1px solid #e9ecef;
}

.motivation-text {
  font-size: 16px;
  font-weight: 700;
  text-align: center;
  padding: 16px;
  height: 54px;
}

.motivation-text-passed {
  background-color: #087f5b;
  color: #fff;
}

.motivation-text-error {
  background-color: #d9480f;
  color: #fff;
}

.text {
  font-size: 21px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: 1px;
  box-shadow: 0 20px 30px 0 rgba(0, 0, 0, 0.07);
}

.passed-text {
  color: #087f5b;
}

.wrong {
  background-color: #d9480f;
  color: #fff;
  padding: 4px 3px;
  border-radius: 4px;
}

.current-text {
  font-weight: 400;
  background-color: #087f5b;
  color: #fff;
  padding: 4px 3px;
  border-radius: 4px;
}

.stat {
  display: flex;
  justify-content: space-between;
}

.stat div {
  text-align: center;
}

.redo {
  color: #d9480f;
}

.redo:hover {
  cursor: pointer;
  font-size: 26px;
}

.redo:active {
  font-size: 20px;
}

.stat-item {
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 500;
  color: #adb5bd;
  letter-spacing: 1px;
  padding-top: 8px;
}

.stat-value {
  font-size: 24px;
  font-weight: 500;
  color: #087f5b;
}

.stat-symbol {
  width: 46px;
}

.stat-speed {
  width: 149px;
}

.stat-time {
  width: 69px;
}

.finish {
  border: 5px solid #087f5b;
}

header {
  align-self: center;
}

.app {
  width: 960px;
  margin: 48px auto;
  display: flex;
  flex-direction: column;
  grid-gap: 24px;
  gap: 24px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  color: #343a40;
  background-color: #f8f9fa;
  border-top: 8px solid #087f5b;
}

